import { store } from '../store'

let Evento = function(start, end, title) {
    this.title = title;
    this.start = start;
    this.end = end;
    this.owner = store.getters.getUser;
    this.textColor = 'white';
    this.borderColor = "#ac9f00";
    this.backgroundColor = "#ac9f00";
}

let addEvento = {
    title: "Deseas agregar el evento?",
    text: "Una vez realizado no se podra modificar!",
    type: "warning",
    allowOutsideClick: false,
    allowEnterKey: false,
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Crear"
}

let nameEvento = {
    title: "Nombre del evento",
    input: "text",
    allowOutsideClick: false,
    allowEnterKey: false,
    showCancelButton: true,
    onClose: function() {
        console.log('cerrado')
    },
    inputValidator: value => {
        if (!value) return "Necesitas escribir algo!";
    }
}

let succEvent = {
    title: "Evento creado!",
    text: "Ahora puedes verlo en el calendario.",
    type: "success"
}

let errEvento = {
    title: "No se pudo crear el evento!",
    text: "Si tienes algun problema, no contactes al admin.",
    type: "error"
}
export { Evento, addEvento, nameEvento, succEvent, errEvento };