import Vue from "vue";
import Vuex from "vuex";
import router from './router'
import axios from './auth-axios'
import jwtDecode from 'jwt-decode';
import persistedState from 'vuex-persistedstate'

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        auth: null,
        token: null
    },
    mutations: {
        setAuth(state, token) {
            state.token = token
        }
    },
    actions: {
        login({ commit }, form) {
            axios.post('/api/login', form).then(resp => {
                console.log(resp)
                if (resp.data.ok) {
                    commit('setAuth', resp.data.token)
                    router.replace('/calendario')
                }
            })
        }
    },
    getters: {
        getAuth(state) {
            return state.token
        },
        getUser(state) {
            return jwtDecode(state.token).usuario._id;
        }
    },
    plugins: [
        persistedState({
            key: 'token',
            paths: ['token']
        })
    ]
});