import Vue from "vue";
import Router from "vue-router";
import { store } from './store'
import calendario from './components/calendar.vue'
import login from './components/login.vue'

Vue.use(Router);

const router = new Router({
    mode: 'history',
    base: process.env.baseUrl,
    routes: [
        { path: '/', name: 'login', component: login, meta: { auth: true } },
        { path: '/calendario', name: 'calendario', component: calendario, meta: { auth: true } }
    ]
})

const auth = {
    loggedIn() {
        return store.getters.getAuth
    },
    logout() {
        store.commit('setLogout')
    }
}

router.beforeEach((to, from, next) => {
    if (auth.loggedIn()) {
        if (to.name === 'login') {
            return next('/calendario')
        } else {
            next()
        }
    } else {
        if (to.name !== 'login') {
            return next('/')
        }
        next()
    }
})

export default router;