<template>
  <div>
    <div class="row">
      <div class="col-md-12">
        <h1>Calendario</h1>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <full-calendar :config="config" :events="events" id="calendar"/>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import conf from "../config/calendar";
import axios from "../auth-axios";
import $ from "jquery";
import {
  Evento,
  addEvento,
  nameEvento,
  errEvento,
  succEvent
} from "../classes/evento";

export default {
  name: "hello",
  data() {
    return {
      color: "#821467",
      today: null,
      events: [],
      ultimos4: [],
      config: {
        eventRender: (event, element, view) => {},
        unselect: (jsEvent, view) => {},
        eventClick: (calEvent, jsEvent, view) => {},
        select: (start, end, jsEvent, view) => {
          let fecha = start.format("YYYY D MM");
          let hora = start.format("HH:mm");
          let now = moment().format("HH:mm");
          if (fecha < this.today || hora < now) {
            $("#calendar").fullCalendar("unselect");
            return;
          }
          start.format("YYYY-MM-DD h:mm:ss");
          end.format("YYYY-MM-DD h:mm:ss");
          this.saveData({ start: start, end: end });
        }
      }
    };
  },
  mounted() {
    this.today = moment().format("YYYY D MM");
    axios.get("/api/calendario").then(resp => {
      if (!resp.data.calendarios) return;
      this.events = resp.data.calendarios;
      console.log(this.events);
      this.ultimos4 = this.events.slice(this.events.length - 6).reverse();
      this.filtrado();
    });
  },
  created() {
    this.config = Object.assign(this.config, conf.config);
  },
  methods: {
    filtrado() {
      let owner = this.$store.getters.getUser;
      console.log(owner);
      this.events.filter(e => {
        if (e.owner._id == owner) {
          e.borderColor = "#ac9f00";
          e.backgroundColor = "#ac9f00";
        } else {
          e.borderColor = "#871e6e";
          e.backgroundColor = "#871e6e";
        }
      });
    } /* format("dddd, h:mm a") */,
    format(createdAt) {
      return `${moment(createdAt).fromNow()}`;
    },
    saveData(data) {
      this.$swal.fire(addEvento).then(result => {
        if (result.value) {
          this.$swal.fire(nameEvento).then(result => {
            data.title = result.value;
            this.sendData(data);
          });
        }
      });
    },
    sendData(data) {
      let evento = new Evento(data.start, data.end, data.title);
      axios.post("/api/calendario", evento).then(resp => {
        if (resp.data.ok) {
          this.$swal.fire(succEvent);
          $("#calendar").fullCalendar("renderEvent", evento);
        } else {
          this.$swal.fire(errEvento);
        }
      });
    }
  }
};
</script>

<style>
.fc-content {
  color: white;
}
.hr-fat {
  width: 100%;
  height: 20px;
  background: #f8f9fa;
  border-radius: 5px;
}
</style>
